import React, { Component } from 'react';

class WomensHealth extends Component {
  render() {
    return (
      <div>
          <h2>Women's Health</h2>
          <hr />
          <div>
            <ul>
                <li>Well woman exams: complete head to toe assessments which include mammograms, cancer screenings, blood work, and PAP smears if necessary</li>
                <li>Pregnancy: planning, counseling and preparation for pregnancy.</li>
                <li>Breastfeeding: counseling and support</li>
                <li>Heart health: We care for the maintenance and well being of your heart by encouraging and guiding you toward a heart healthy lifestyle. We also care for active cardiac disease to minimize its impact on your lifestyle, improve your quality of life, and increase your longevity with the minimal amount of medication and intervention possible.</li>
                <li>Menopause: Counseling and guidance for improving the golden years of a woman’s life. We promote a lifestyle which is consistent with your ideals and beliefs to reduce the issues that may affect the quality of life as a woman ages. We are well versed at using traditional as well as alternative forms of medicine to achieve the goals that our patients desire.</li>
                <li>Osteoporosis: our goal is to prevent the disfiguring, disabling, and dangerous effects of osteoporosis that occurs over time.</li>
                <li>Mental health: Counseling and treatment is available for many common issues which include depression, anxiety, bereavement, and insomnia. Treatment encompasses more than just medicines, but one on one counseling as well as lifestyle changes.</li>
                <li>Quitting smoking: there are several successful methods to successfully quit smoking. We would love to help you stop, all you need to do is ask.</li>
                <li>Nutrition: A balanced diet of moderation is the key to a healthy diet. We can help to achieve this if you want. We are also experts at diagnosing vitamin deficiencies and will show you how to properly replace theses missing nutrients in your diet in a way that is comfortable for you.</li>
                <li>Exercise: Exercise is well known to be the silver bullet that can prevent and cure most chronic diseases that afflict American’s today. These include Heart disease, Many forms of cancer, High blood pressure, Obesity, Fibromyalgia, Chronic Fatigue Syndrome, and Diabetes. We show you the right way to start and maintain a healthy and safe exercise regimen to improve and maintain your body, mind and spirit, and prevent many debilitating chronic illnesses.</li>
                <li>Contraception and Family Planning: Starting and planning a family is different for every woman. We will work with you to help you achieve your goals. Whether you are planning to start of exand your family now or later, or avoid pregnancy altogether, we have many solution that are available to you.</li>
                <li>Gynecology: From cervical cancer screening and treatment and prevention, to STD counseling, prevention, and treatment, to issues of menstruation (periods), we can provide complete care in one place.</li>
                <li>Issues of Feminine Aging: For many years most women just accepted many of the signs of aging as inevitable and untreatable. This is not the case for many of these issues which range from sexual function, osteoporosis, hot flashes, libido, mood swings, forgetfulness, and skin problems. Many of these conditions can be successfully treated without medications.</li>
                <li>Sexually Transmitted Diseases: Prevention, treatment and counseling.</li>
            </ul>
          </div>
      </div>
    );
  }
}

export default WomensHealth;


